"use client";

import React, { useState, useEffect, useCallback, JSX } from "react";
import { useSearchParams } from "next/navigation";
import { Card, CardHeader, CardContent, CardTitle, CardDescription, CardFooter } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import {
  FileUp,
  Loader,
  FileText,
  FileIcon,
  Download,
  RefreshCw,
  Upload,
  CheckCircle2,
  AlertCircle,
  Trash2,
  Clock,
  CheckCircle,
  XCircle,
} from "lucide-react";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { Label } from "@/components/ui/label";
import { Badge } from "@/components/ui/badge";
import { Progress } from "@/components/ui/progress";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";

// Types
interface DocumentValidation {
  validationStatus: "validated" | "rejected" | "pending";
  comment: string | null;
}

interface SubmittedDocument {
  idPFE: number;
  idLivrable: 1 | 2;
  fichier: string;
  tutorValidation?: DocumentValidation | null;
  profValidation?: DocumentValidation | null;
}

interface LivrableNames {
  [key: number]: string;
}

// Constants
const API_BASE_URL = "http://localhost:5000";
const LIVRABLE_NAMES: LivrableNames = {
  1: "Document PDF",
  2: "Présentation",
};

const LIVRABLE_ICONS = {
  1: <FileText className="h-5 w-5" />,
  2: <FileIcon className="h-5 w-5" />,
};

export default function SoumettrePage() {
  const searchParams = useSearchParams();
  const idEtudiant = searchParams.get("idEtudiant") || "4";
  const idGroupe = searchParams.get("idGroupe") || "2";

  // State
  const [pdfFile, setPdfFile] = useState<File | null>(null);
  const [presentationFile, setPresentationFile] = useState<File | null>(null);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitSuccess, setSubmitSuccess] = useState(false);
  const [submitError, setSubmitError] = useState<string | null>(null);
  const [submittedDocuments, setSubmittedDocuments] = useState<SubmittedDocument[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [fetchError, setFetchError] = useState<string | null>(null);
  const [uploadProgress, setUploadProgress] = useState(0);
  const [activeTab, setActiveTab] = useState("submitted");

  // Fetch documents from API
  const fetchDocuments = useCallback(async () => {
    setIsLoading(true);
    setFetchError(null);
    try {
      const response = await fetch(`${API_BASE_URL}/api/livrable/documents?idEtudiant=${idEtudiant}`);
      if (response.ok) {
        const data = (await response.json()) as SubmittedDocument[];
        setSubmittedDocuments(data);
      } else {
        setFetchError("Impossible de récupérer les documents");
      }
    } catch (error) {
      setFetchError("Erreur lors de la récupération des documents");
      console.error("Fetch error:", error);
    } finally {
      setIsLoading(false);
    }
  }, [idEtudiant]);

  useEffect(() => {
    fetchDocuments();
  }, [fetchDocuments]);

  // File handling
  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>, setFile: (file: File | null) => void) => {
    if (e.target.files?.[0]) {
      setFile(e.target.files[0]);
      simulateUploadProgress();
    }
  };

  const simulateUploadProgress = () => {
    setUploadProgress(0);
    const timer = setInterval(() => {
      setUploadProgress((prev) => {
        if (prev >= 100) {
          clearInterval(timer);
          return 100;
        }
        return prev + 10;
      });
    }, 100);
  };

  const resetForm = () => {
    setPdfFile(null);
    setPresentationFile(null);
    setUploadProgress(0);
    const pdfInput = document.getElementById("pdf") as HTMLInputElement;
    const presentationInput = document.getElementById("presentation") as HTMLInputElement;
    if (pdfInput) pdfInput.value = "";
    if (presentationInput) presentationInput.value = "";
  };

  // Form submission
  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setSubmitSuccess(false);
    setSubmitError(null);
    setIsSubmitting(true);
    setUploadProgress(0);

    if (!pdfFile || !presentationFile) {
      setSubmitError("Veuillez sélectionner les deux fichiers.");
      setIsSubmitting(false);
      return;
    }

    const formData = new FormData();
    formData.append("files", pdfFile);
    formData.append("files", presentationFile);
    formData.append("idEtudiant", idEtudiant);
    formData.append("idGroupe", idGroupe);
    formData.append("livrables", JSON.stringify([1, 2]));

    try {
      const response = await fetch(`${API_BASE_URL}/api/livrable/submit-documents`, {
        method: "POST",
        body: formData,
      });

      if (response.ok) {
        setSubmitSuccess(true);
        resetForm();
        fetchDocuments();
        setActiveTab("submitted");
      } else {
        const errorText = await response.text();
        setSubmitError(`Erreur lors de la soumission: ${errorText}`);
      }
    } catch (error) {
      console.error("Erreur de connexion:", error);
      setSubmitError("Erreur de connexion au serveur");
    } finally {
      setIsSubmitting(false);
    }
  };

  // Document deletion
  const handleDelete = async (idPFE: number, idLivrable: number) => {
    if (!confirm("Êtes-vous sûr de vouloir supprimer ce document ?")) {
      return;
    }

    try {
      const response = await fetch(`${API_BASE_URL}/api/livrable/delete-document`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ idEtudiant, idPFE, idLivrable }),
      });

      if (response.ok) {
        fetchDocuments();
      } else {
        const errorText = await response.text();
        alert(`Erreur lors de la suppression : ${errorText}`);
      }
    } catch (error) {
      console.error("Erreur de connexion:", error);
      alert("Erreur de connexion au serveur");
    }
  };

  const normalizeStatus = (status: string) =>
    status.toLowerCase().trim();
  
  const getStatusIcon = (status: string) => {
    const normalized = normalizeStatus(status);
    switch (normalized) {
      case "validated":
        return <CheckCircle className="h-4 w-4 text-green-500" />;
      case "rejected":
        return <XCircle className="h-4 w-4 text-red-500" />;
      case "pending":
        return <Clock className="h-4 w-4 text-amber-500" />;
      default:
        return <Clock className="h-4 w-4 text-amber-500" />;
    }
  };
  
  const getStatusText = (status: string) => {
    const normalized = normalizeStatus(status);
    switch (normalized) {
      case "validated":
        return "Validé";
      case "rejected":
        return "Rejeté";
      case "pending":
        return "En attente";
      default:
        return status;
    }
  };
  
  const getStatusColor = (status: string) => {
    const normalized = normalizeStatus(status);
    switch (normalized) {
      case "validated":
        return "bg-green-50 text-green-700 border-green-200 dark:bg-green-900/20 dark:text-green-400 dark:border-green-800";
      case "rejected":
        return "bg-red-50 text-red-700 border-red-200 dark:bg-red-900/20 dark:text-red-400 dark:border-red-800";
      case "pending":
        return "bg-amber-50 text-amber-700 border-amber-200 dark:bg-amber-900/20 dark:text-amber-400 dark:border-amber-800";
      default:
        return "bg-amber-50 text-amber-700 border-amber-200 dark:bg-amber-900/20 dark:text-amber-400 dark:border-amber-800";
    }
  };
  

  return (
    <div className="container mx-auto py-8 px-4">
      <Card className="w-full max-w-3xl mx-auto shadow-lg border-0 overflow-hidden bg-gradient-to-b from-background to-muted/20">
        <CardHeader className="pb-6 border-b border-border/40 bg-muted/30">
          <CardTitle className="text-2xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-primary to-primary/80">
            Soumettre des documents
          </CardTitle>
          <CardDescription className="text-muted-foreground">
            Soumettez vos documents de projet pour évaluation
          </CardDescription>
        </CardHeader>

        <CardContent className="p-0">
          {/* Success and Error Alerts */}
          {submitSuccess && <SubmissionAlert type="success" title="Succès" message="Documents soumis avec succès!" />}
          {submitError && <SubmissionAlert type="error" title="Erreur" message={submitError} />}

          <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
            <div className="px-6 pt-6">
              <TabsList className="w-full grid grid-cols-2">
                <TabsTrigger value="submitted" className="text-sm">
                  Documents Soumis
                </TabsTrigger>
                <TabsTrigger value="upload" className="text-sm">
                  Soumettre des Documents
                </TabsTrigger>
              </TabsList>
            </div>

            {/* Submitted Documents Tab */}
            <TabsContent value="submitted" className="p-6 space-y-6">
              <SubmittedDocumentsTab
                isLoading={isLoading}
                fetchError={fetchError}
                submittedDocuments={submittedDocuments}
                fetchDocuments={fetchDocuments}
                handleDelete={handleDelete}
                setActiveTab={setActiveTab}
                livrableIcons={LIVRABLE_ICONS}
                livrableNames={LIVRABLE_NAMES}
                getStatusIcon={getStatusIcon}
                getStatusText={getStatusText}
                getStatusColor={getStatusColor}
              />
            </TabsContent>

            {/* Upload Documents Tab */}
            <TabsContent value="upload" className="p-6 space-y-6">
              <UploadDocumentsTab
                handleSubmit={handleSubmit}
                handleFileChange={handleFileChange}
                pdfFile={pdfFile}
                setPdfFile={setPdfFile}
                presentationFile={presentationFile}
                setPresentationFile={setPresentationFile}
                uploadProgress={uploadProgress}
                isSubmitting={isSubmitting}
              />
            </TabsContent>
          </Tabs>
        </CardContent>

        <CardFooter className="p-4 border-t border-border/40 bg-muted/30 flex justify-between">
          <p className="text-xs text-muted-foreground">• ID Groupe: {idGroupe}</p>
          <p className="text-xs text-muted-foreground">Dernière mise à jour: {new Date().toLocaleDateString()}</p>
        </CardFooter>
      </Card>
    </div>
  );
}

// Component for alerts
interface SubmissionAlertProps {
  type: "success" | "error";
  title: string;
  message: string;
}

function SubmissionAlert({ type, title, message }: SubmissionAlertProps) {
  return (
    <Alert
      className={
        type === "success"
          ? "m-6 bg-green-50 dark:bg-green-900/20 text-green-800 dark:text-green-300 border-green-200 dark:border-green-800"
          : "m-6 bg-destructive/10 text-destructive border-destructive/20"
      }
    >
      {type === "success" ? <CheckCircle2 className="h-4 w-4" /> : <AlertCircle className="h-4 w-4" />}
      <AlertTitle className="font-medium">{title}</AlertTitle>
      <AlertDescription>{message}</AlertDescription>
    </Alert>
  );
}

// Component for the Submitted Documents tab
interface SubmittedDocumentsTabProps {
  isLoading: boolean;
  fetchError: string | null;
  submittedDocuments: SubmittedDocument[];
  fetchDocuments: () => Promise<void>;
  handleDelete: (idPFE: number, idLivrable: number) => Promise<void>;
  setActiveTab: (tab: string) => void;
  livrableIcons: Record<number, JSX.Element>;
  livrableNames: LivrableNames;
  getStatusIcon: (status: string) => JSX.Element;
  getStatusText: (status: string) => string;
  getStatusColor: (status: string) => string;
}

function SubmittedDocumentsTab({
  isLoading,
  fetchError,
  submittedDocuments,
  fetchDocuments,
  handleDelete,
  setActiveTab,
  livrableIcons,
  livrableNames,
  getStatusIcon,
  getStatusText,
  getStatusColor,
}: SubmittedDocumentsTabProps) {
  return (
    <>
      <div className="flex items-center justify-between">
        <h2 className="text-lg font-semibold flex items-center gap-2">
          <FileText className="h-5 w-5 text-primary" />
          Documents Soumis
        </h2>
        <Button
          variant="outline"
          size="sm"
          onClick={fetchDocuments}
          disabled={isLoading}
          className="h-8 hover:bg-primary/5 hover:text-primary"
        >
          <RefreshCw className={`h-3.5 w-3.5 mr-1.5 ${isLoading ? "animate-spin" : ""}`} />
          Actualiser
        </Button>
      </div>

      {isLoading ? (
        <div className="flex items-center justify-center p-8 bg-muted/10 rounded-xl border border-border/40">
          <Loader className="h-5 w-5 animate-spin mr-2 text-primary" />
          <p className="text-muted-foreground">Chargement des documents...</p>
        </div>
      ) : fetchError ? (
        <Alert variant="destructive" className="bg-destructive/10 text-destructive border-destructive/20">
          <AlertCircle className="h-4 w-4" />
          <AlertDescription>{fetchError}</AlertDescription>
        </Alert>
      ) : submittedDocuments.length > 0 ? (
        <div className="space-y-4">
          {submittedDocuments.map((doc) => (
            <DocumentCard
              key={`${doc.idPFE}-${doc.idLivrable}`}
              document={doc}
              handleDelete={handleDelete}
              livrableIcons={livrableIcons}
              livrableNames={livrableNames}
              getStatusIcon={getStatusIcon}
              getStatusText={getStatusText}
              getStatusColor={getStatusColor}
            />
          ))}
        </div>
      ) : (
        <div className="flex flex-col items-center justify-center p-8 bg-muted/10 rounded-xl border border-border/40">
          <Upload className="h-8 w-8 text-muted-foreground mb-2" />
          <p className="text-muted-foreground text-center">Aucun document soumis pour le moment.</p>
          <Button variant="link" onClick={() => setActiveTab("upload")} className="mt-2 text-primary">
            Soumettre maintenant
          </Button>
        </div>
      )}
    </>
  );
}

// Component for individual document cards
interface DocumentCardProps {
  document: SubmittedDocument;
  handleDelete: (idPFE: number, idLivrable: number) => Promise<void>;
  livrableIcons: Record<number, JSX.Element>;
  livrableNames: LivrableNames;
  getStatusIcon: (status: string) => JSX.Element;
  getStatusText: (status: string) => string;
  getStatusColor: (status: string) => string;
}

function DocumentCard({
  document: doc,
  handleDelete,
  livrableIcons,
  livrableNames,
  getStatusIcon,
  getStatusText,
  getStatusColor,
}: DocumentCardProps) {
  return (
    <Card className="overflow-hidden border border-border/40 hover:shadow-md transition-all duration-300 hover:border-primary/20">
      <div className="flex items-center justify-between p-4 bg-muted/20 border-b border-border/30">
        <div className="flex items-center gap-3">
          <div className="bg-primary/10 p-2.5 rounded-full">
            {doc.idLivrable in livrableIcons && livrableIcons[doc.idLivrable as keyof typeof livrableIcons]}
          </div>
          <div>
            <span className="font-medium">{livrableNames[doc.idLivrable]}</span>
            <Badge variant="secondary" className="ml-2 text-xs">
              Soumis
            </Badge>
          </div>
        </div>
        <div className="flex gap-2">
          <Button variant="ghost" size="sm" asChild className="hover:bg-primary/5 hover:text-primary transition-colors">
            <a href={`http://localhost:5000${doc.fichier}`} download>
              <Download className="h-4 w-4 mr-2" />
              Télécharger
            </a>
          </Button>
          <Button
            variant="ghost"
            size="sm"
            onClick={() => handleDelete(doc.idPFE, doc.idLivrable)}
            className="text-red-500 hover:text-red-700 hover:bg-red-50 dark:hover:bg-red-900/20"
          >
            <Trash2 className="h-4 w-4" />
          </Button>
        </div>
      </div>

      <div className="p-4 space-y-3">
  {doc.tutorValidation || doc.profValidation ? (
    <>
      {doc.tutorValidation && (
        <ValidationStatus
          type="Tuteur"
          validation={doc.tutorValidation}
          getStatusIcon={getStatusIcon}
          getStatusText={getStatusText}
          getStatusColor={getStatusColor}
        />
      )}
      {doc.profValidation && (
        <ValidationStatus
          type="Professeur"
          validation={doc.profValidation}
          getStatusIcon={getStatusIcon}
          getStatusText={getStatusText}
          getStatusColor={getStatusColor}
        />
      )}
    </>
  ) : (
    <div className="flex items-center gap-2 text-sm text-muted-foreground">
      <Clock className="h-4 w-4" />
      <span>Pas encore validé</span>
    </div>
  )}
</div>
    </Card>
  );
}

// Component for validation status
interface ValidationStatusProps {
  type: "Tuteur" | "Professeur";
  validation: DocumentValidation;
  getStatusIcon: (status: string) => JSX.Element;
  getStatusText: (status: string) => string;
  getStatusColor: (status: string) => string;
}

function ValidationStatus({ type, validation, getStatusIcon, getStatusText, getStatusColor }: ValidationStatusProps) {
  return (
    <div className={`p-3 rounded-lg ${getStatusColor(validation.validationStatus)}`}>
      <div className="flex items-center gap-2 mb-1">
        {getStatusIcon(validation.validationStatus)}
        <span className="font-medium">{type}</span>
        <Badge variant="outline" className="ml-auto text-xs">
          {getStatusText(validation.validationStatus)}
        </Badge>
      </div>
      {validation.comment && <p className="text-sm mt-1 pl-6">{validation.comment}</p>}
    </div>
  );
}

// Component for the Upload Documents tab
interface UploadDocumentsTabProps {
  handleSubmit: (e: React.FormEvent<HTMLFormElement>) => Promise<void>;
  handleFileChange: (e: React.ChangeEvent<HTMLInputElement>, setFile: (file: File | null) => void) => void;
  pdfFile: File | null;
  setPdfFile: (file: File | null) => void;
  presentationFile: File | null;
  setPresentationFile: (file: File | null) => void;
  uploadProgress: number;
  isSubmitting: boolean;
}

function UploadDocumentsTab({
  handleSubmit,
  handleFileChange,
  pdfFile,
  setPdfFile,
  presentationFile,
  setPresentationFile,
  uploadProgress,
  isSubmitting,
}: UploadDocumentsTabProps) {
  return (
    <>
      <div className="flex items-center gap-2 mb-4">
        <FileUp className="h-5 w-5 text-primary" />
        <h2 className="text-lg font-semibold">Soumettre de nouveaux documents</h2>
      </div>

      <form onSubmit={handleSubmit} className="space-y-6">
        {[
          {
            id: "pdf",
            label: "Document PDF",
            icon: <FileText />,
            state: pdfFile,
            setState: setPdfFile,
            accept: ".pdf",
          },
          {
            id: "presentation",
            label: "Présentation",
            icon: <FileIcon />,
            state: presentationFile,
            setState: setPresentationFile,
            accept: ".ppt,.pptx,.key,.pdf",
          },
        ].map((item) => (
          <FileUploadCard
            key={item.id}
            id={item.id}
            label={item.label}
            icon={item.icon}
            state={item.state}
            setState={item.setState}
            accept={item.accept}
            handleFileChange={handleFileChange}
            uploadProgress={uploadProgress}
          />
        ))}

        <Button type="submit" className="w-full relative overflow-hidden group" disabled={isSubmitting} size="lg">
          <div
            className="absolute inset-0 bg-gradient-to-r from-primary/0 via-primary/10 to-primary/0 
            animate-shimmer -translate-x-full group-hover:translate-x-full transition-all duration-1000"
          />
          {isSubmitting ? (
            <>
              <Loader className="mr-2 h-5 w-5 animate-spin" />
              Soumission en cours...
            </>
          ) : (
            <>
              <FileUp className="mr-2 h-5 w-5" />
              Soumettre les documents
            </>
          )}
        </Button>
      </form>
    </>
  );
}

// Component for file upload cards
interface FileUploadCardProps {
  id: string;
  label: string;
  icon: JSX.Element;
  state: File | null;
  setState: (file: File | null) => void;
  accept: string;
  handleFileChange: (e: React.ChangeEvent<HTMLInputElement>, setFile: (file: File | null) => void) => void;
  uploadProgress: number;
}

function FileUploadCard({
  id,
  label,
  icon,
  state,
  setState,
  accept,
  handleFileChange,
  uploadProgress,
}: FileUploadCardProps) {
  return (
    <Card className="overflow-hidden">
      <CardHeader className="py-3 px-4 bg-muted/20 border-b border-border/30">
        <Label htmlFor={id} className="flex items-center gap-2 font-medium cursor-pointer">
          {React.cloneElement(icon, { className: "h-4 w-4" })}
          {label}
        </Label>
      </CardHeader>
      <CardContent className="p-4">
        <div className="relative">
          <input
            type="file"
            id={id}
            accept={accept}
            onChange={(e) => handleFileChange(e, setState)}
            className="block w-full text-sm text-muted-foreground
              file:mr-4 file:py-2.5 file:px-4
              file:rounded-lg file:border-0
              file:text-sm file:font-medium
              file:bg-primary file:text-primary-foreground
              hover:file:bg-primary/90 cursor-pointer
              border rounded-lg focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring"
            required
          />
          {state && (
            <>
              <Badge className="absolute right-3 top-1/2 transform -translate-y-1/2 bg-primary/10 text-primary border-primary/20 px-2">
                {state.name.length > 20 ? `${state.name.substring(0, 20)}...` : state.name}
              </Badge>
              <Progress value={uploadProgress} className="h-1 absolute bottom-0 left-0 right-0" />
            </>
          )}
        </div>
      </CardContent>
    </Card>
  );
}